@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../../fonts/rouble.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../../fonts/roboto-condensed-v27-cyrillic_latin-300.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/roboto-condensed-v27-cyrillic_latin-regular.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/roboto-condensed-v27-cyrillic_latin-700.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  src: url("../../fonts/alumni-sans-v18-cyrillic_latin-600.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 800;
  src: url("../../fonts/alumni-sans-v18-cyrillic_latin-800.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  user-select: none;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  width: 100%;
  height: 100%;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 45px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 103px;
  }
}

.btn {
  display: inline-flex;
  color: #ffffff;
  border: 2px solid #ffffff;
  background-color: inherit;
  padding: 27px 56px;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
  letter-spacing: 0.04rem;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
@media (hover: hover) and (hover: hover) {
  .btn:hover:not(.focus-visible):hover:not(.focus-visible) {
    background-color: #ffffff;
    color: #103aae;
  }
}
@media (hover: hover) {
  .btn.focus-visible:focus:hover:not(.focus-visible), .btn:focus-visible:hover:not(.focus-visible) {
    background-color: #ffffff;
    color: #103aae;
  }
}
.btn:active:not(.focus-visible):active:not(.focus-visible) {
  color: #102654;
}
.btn.focus-visible:focus {
  outline: 2px solid red;
  color: #103aae;
  background-color: #ffffff;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.hero .hero__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid grey;
}
@media (min-width: 1440px) {
  .hero .hero__container {
    padding-top: 20px;
    padding-bottom: 20px;
    border: none;
  }
}
.hero img {
  width: 120px;
  height: 28px;
  object-fit: contain;
}
@media (min-width: 768px) {
  .hero img {
    width: 220px;
    height: 48px;
  }
}
@media (min-width: 1440px) {
  .hero img {
    width: 300px;
    height: 68px;
  }
}
.hero a {
  font-size: 20px;
  line-height: 20px;
  font-weight: 700;
  padding-top: 4px;
}
@media (min-width: 768px) {
  .hero a {
    font-size: 35px;
    line-height: 35px;
  }
}
@media (min-width: 1440px) {
  .hero a {
    padding-top: 14px;
    font-size: 45px;
    line-height: 45px;
    font-weight: 700;
    background: linear-gradient(128deg, #D10a42 0%, #0041ca 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}